@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.input-wrapper {
    display: flex;
    justify-content: center;
    padding: 20px;
    margin-top: 40px;
    width: 100%;
}

input {
    width: 100%;
    margin: 0 1em;
    max-width: 522px;
    height: 48px;
    font-size: inherit;
    padding: 0 58px 0 28px;
    border-radius: 30px;
    border: none;
    border: 1.3px solid #f2f2f2;
    outline: 0;
    box-shadow: 2px 4px 8px 0 rgba(0, 0, 0, 0.15);
    transition: all 300ms ease;
}

.search-warning {
    font-size: 22px;
}
.mCard {
    display: flex;
    flex-direction: column;
    position: relative;
    max-width: 350px;
    height: 630px;
    margin: 10px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 0 0 .5em .5em;
}

.mCard .icon {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    color: #fff700;
}

.mCard img {
    width: 100%;
    transition: width .3s;
}

.mCard .img-wrapper {
    border-radius: .5em .5em 0 0;
    width: 100%;
    padding-top: 150%;
    overflow: hidden;
    position: relative;
}

.mCard .img-wrapper>div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: top .3s;
}

.mCard .info {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.mCard .info .title {
    font-size: 22px;
    text-align: center;
}

.mCard .info .year {
    font-size: 25px;
    font-weight: 500;
}

.mCard:hover img {
    width: 110%;
    top: -10%
}

.mCard:hover .img-wrapper>div {
    top: -5%;
}
.movies {
    width: 100%;
}

.home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.search-warning {
    margin: 20px 0;
    color: white;
}

.text-link {
    text-decoration: none;
    color: inherit;
}
.detail-container {
    margin-top: 40px;
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    color: rgba(20, 20, 20, 0.6);
    background-color: rgba(255, 255, 255, 0.75);
}

.info {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
}

.field .title {
    font-weight: 500;
    font-size: 2em;
}

.field .label {
    font-weight: 500;
    font-size: 1.25em;
}

.field .label .label-p {
    font-weight: 300;
    font-size: 1em;
    display: inline;
}

.genre-label {
    margin-right: .25em;
}

.label-overview {
    font-weight: 400;
}

.poster img {
    border-radius: .5em;
    width: 100%;
}

.movie-infos {
    display: flex;
    flex-direction: column;
    margin-bottom: 1em;
}

.movie-infos .label {
    font-weight: 500;
    font-size: 1.25em;
    text-align: center;
}

.movie-infos #value {
    font-weight: 500;
    font-size: 1.1em;
}
header {
    background-color: rgba(200, 200, 200, 0.9);
}

nav {
    display: flex;
    justify-content: space-between;
    font-weight: 500;
}

.logo {
    display: flex;
    align-items: center;
    padding: 10px;
    border: none;
    color: rgb(0, 0, 0);
    font-size: 28px;
}

.nav-item {
    display: flex;
    justify-content: center;
    align-items: center;
}

li,
:link {
    display: inline-block;

}

ul {
    margin: 0;
    padding: 0;
}

.link {
    text-decoration: none;
    padding: 10px 5px;
    border-radius: 5px;
}

li:not(:last-child) :link {
    margin-right: 25px;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  min-height: 100vh;
  background-position: center;
  background-size: cover;
}

main {
  padding-bottom: 50px;
}

#root {
  position: relative;
}

.background-wrapper {
  position: absolute;
  overflow: hidden;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -100;
  top: 0;
  min-height: 100vh;
  padding-left: -5%;
}

#background_image {
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  width: 110%;
  height: 110%;
  -webkit-filter: blur(10px);
          filter: blur(10px);
  margin-left: -5%;
}

a {
  color: black;
}

a:hover {
  color: rgb(75, 75, 75);
  font-weight: 400;
}
